import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Thankssegment extends React.Component {

	componentDidMount() {
		document.title = '1. část trasy z celkových 21 částí'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Děkuji že pomáháš udržovat data o trase aktuální a navigaci kvalitnější.</p>
				<p className="text">1. část trasy z celkových 21 částí. Jsi teď na adrese Myslíkova 1134/9. Jdi asi 70 metrů rovně mírně z kopce k zaoblenému rohu s ulicí Cháberova.</p>
				<form action="">
					<Navlink value="Další část" to="#/crowdsourcing"/>
					<Navlink value="Předchozí část" to="#/prevsegment"/>
					<Navlink value="Zkontrolovat polohu" to="#/lost"/>
					<Navlink value="Nahlásit problém na trase" to="#/report"/>
					<Navlink value="Zpět na hledání" to="#/"/>
				</form>
		  </div>
		);
	}
}



