import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Walk extends React.Component {

	componentDidMount() {
		document.title = 'Zpřesnění GPS'
	}

	render() {
		return (
			<div className="main">
		      <p className="text focusable" tabIndex="-1">Aby se zvýšila přesnost GPS, potřebuji aby jsi ušel pár metrů. Řeknu ti kdy přestat.</p>
					<p className="text">Poznámka: Tlačítko "další krok" se objeví pouze po upřesnění GPS lokace.</p>
		      <form action="">
						<Navlink value="Další krok" to="#/roadside"/>
		        <Navlink value="Zpět na hledání" to="#/"/>
		      </form>
		  </div>
		);
	}
}



