import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Report extends React.Component {

	componentDidMount() {
		document.title = 'Nahlášení problému na trase'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Blokuje ti něco cestu?</p>
				<form action="">
					<Navlink value="Ano" to="#/reportblock"/>
					<Navlink value="Ne" to="#/reportother"/>
					<Navlink value="Zpět do navigace" to="#/segment"/>
				</form>
		  </div>
		);
	}
}



