import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Lost extends React.Component {

	componentDidMount() {
		document.title ='Ověření polohy'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Pravděpodobně jsi mimo původní trasu. Upřesním tvou lokaci a najdu novou trasu.</p>
				<form action="">
					<Navlink value="Dobře" to="#/walk"/>
					<Navlink value="Zpět do navigace" to="#/segment"/>
				</form>
		  </div>
		);
	}
}



