import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Startnavigation extends React.Component {

	componentDidMount() {
		document.title = 'Trasa nalezena'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Trasa z adresy Myslíkova 1134/9 na Karlovo náměstí 1623/6. Trasa je přibližně 1100 metrů dlouhá a vede přes 5 přechodů. Postav se tak, že budeš mít vozovku po své pravé straně.</p>
				<form action="">
					<Navlink value="Začít navigaci" to="#/segment"/>
					<Navlink value="Zpět na hledání" to="#/"/>
				</form>
		  </div>
		);
	}
}



