import 'core-js/fn/object/assign';
// import 'core-js';
import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, /*Redirect,*/ useRouterHistory, IndexRoute } from 'react-router';
import createHashHistory from 'history/lib/createHashHistory';
//import createBrowserHistory from 'history/lib/createBrowserHistory';

import Links from './actions/Links.js'

import App from 'components/App';
import Home from 'components/Home';
import Multiple from 'components/Multiple';
import Usegps from 'components/Usegps';
import Walk from 'components/Walk';
import Roadside from 'components/Roadside';
import Startnavigation from 'components/Startnavigation';
import Segment from 'components/Segment';
import Crowdsourcing from 'components/Crowdsourcing';
import Thankssegment from 'components/Thankssegment';
import Lost from 'components/Lost';
import Report from 'components/Report';
import Reportblock from 'components/Reportblock';
import Reportother from 'components/Reportother';

const routes = (
	<Route path="/" component={App}>
		<IndexRoute component={Home} name="home" />
		<Route path="multiple" component={Multiple} name="multiple" />
		<Route path="usegps" component={Usegps} name="usegps" />
		<Route path="walk" component={Walk} name="walk" />
		<Route path="roadside" component={Roadside} name="roadside" />
		<Route path="startnavigation" component={Startnavigation} name="startnavigation" />
		<Route path="segment" component={Segment} name="segment" />
		<Route path="prevsegment" component={Segment} name="prevsegment" />
		<Route path="crowdsourcing" component={Crowdsourcing} name="crowdsourcing" />
		<Route path="thankssegment" component={Thankssegment} name="thankssegment" />
		<Route path="lost" component={Lost} name="lost" />
		<Route path="report" component={Report} name="report" />
		<Route path="reportblock" component={Reportblock} name="reportblock" />
		<Route path="reportother" component={Reportother} name="reportother" />
	</Route>
);

//const routeHistory = useRouterHistory(createBrowserHistory)({ queryKey: false });
const routeHistory = useRouterHistory(createHashHistory)({ queryKey: false });

const createElement = (Component, props) => {
	return <Component key={`${props.route.name}RouteComponent`} {...props}/>
}

ReactDOM.render((
	<Router history={routeHistory} children={routes} createElement={createElement} onUpdate={Links.updated} />
), document.getElementsByClassName('app')[0])



