import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Crowdsourcing extends React.Component {

	componentDidMount() {
		document.title = 'Dodatečný sběr dat o trase'
	}

	render() {
		return (
			<div className="main">
		      <p className="text focusable" tabIndex="-1">Doplňující otázka: Byl ten roh, kolem kterého jsem tě teď navigoval spíše ostrý nebo zaoblený?</p>
		      <form action="">
		        <Navlink value="Ostrý" to="#/thankssegment"/>
		        <Navlink value="Zaoblený" to="#/thankssegment"/>
		        <Navlink value="Nevím" to="#/thankssegment"/>
		        <Navlink value="Zpět na hledání" to="#/"/>
		      </form>
		  </div>
		);
	}
}



