import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Multiple extends React.Component {

	componentDidMount() {
		document.title = 'Nalezeno více adres'
	}

	render() {
		return (
			<div className="main">
		      <p className="text focusable" tabIndex="-1">Našel jsem více adres, vyber jednu z nich prosím.</p>
		      <form action="">
		        <Navlink value="Dlouhá 3859/12" to="#/usegps"/>
						<Navlink value="Jungmannova 680/25" to="#/usegps"/>
						<Navlink value="Karlovo náměstí 1623/6" to="#/usegps"/>
						<Navlink value="Zpět na hledání" to="#/"/>
		      </form>
		  </div>
		);
	}
}



