import React from 'react';
import Links from '../actions/Links.js'

export default class Navlink extends React.Component {

	render() {

		const { value, to, tabIndex, className } = this.props

		return (
      <input type="button" value={value} onClick={(e) => {Links.navlinkClicked(e, to)}} tabIndex={tabIndex} className={className}/>
		);
	}
}



