import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Roadside extends React.Component {

	componentDidMount() {
		document.title = 'Po které ruce je vozovka'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Už to bude, jen ještě potřebuji vědět po které ruce máš momentálně vozovku.</p>
				<form action="">
					<Navlink value="Vlevo" to="#/startnavigation"/>
					<Navlink value="Vpravo" to="#/startnavigation"/>
					<Navlink value="Žádná vozovka tu není" to="#/startnavigation"/>
					<Navlink value="Zpět na hledání" to="#/"/>
				</form>
		  </div>
		);
	}
}



