import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Reportother extends React.Component {

	componentDidMount() {
		document.title = 'Nahlášení neblokujícího problému na trase'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Jaký problém se tedy na trase objevil?</p>
	      <form action="">
	        {/* <select name="kind" id="kind" aria-label="Vyberte prosím typ problému" required>
						<option value="">Typ problému</option>
	          <option value="inappropriate">Cesta je nevhodná</option>
	          <option value="badinfo">Informace o trase nesedí</option>
	          <option value="other">Jiné</option>
	        </select> */}
					<div className="radios">
						<p className="text">Vyberte prosím typ problému</p>
						<input type="radio" name="kind" id="kind1" aria-labelledby="kind1label" value="Cesta je nevhodná" required/>
						<label htmlFor="kind1" id="kind1label" aria-hidden="true">Cesta je nevhodná</label>
						<input type="radio" name="kind" id="kind2" aria-labelledby="kind2label" value="Informace o trase nesedí" required/>
						<label htmlFor="kind2" id="kind2label" aria-hidden="true">Informace o trase nesedí</label>
						<input type="radio" name="kind" id="kind3" aria-labelledby="kind3label" value="Jiné" required/>
						<label htmlFor="kind3" id="kind3label" aria-hidden="true">Jiné</label>
					</div>

	        <label htmlFor="description" id="descriptionlabel" className="inputlabel" aria-hidden="true">Krátký popis (nepovinné):</label>
	        <textarea name="description" id="description" aria-labelledby="descriptionlabel" rows="4" cols="80" className="inputside"></textarea>
					<Navlink value="Nahlásit" to="#/thankssegment"/>
	        <Navlink value="Zpět do navigace" to="#/segment"/>
	      </form>
		  </div>
		);
	}
}



