import React from 'react'
import Navlink from '../components/Navlink.js'

const logo = require('../images/naviterierlogo.png')

export default class Home extends React.Component {

	componentDidMount() {
		document.title = 'Hledání trasy'
	}

	render() {
		return (
			<div className="main">
		    <div className="logo focusable" tabIndex="-1">
					<img aria-hidden="true" src={logo} alt="Logo Naviterier" />
		      <h1>Naviterier - Hledání trasy</h1>
		    </div>
		    <div>
		      <p className="text">Ahoj, jsem Naviterier. Pověz mi cíl své trasy v podobě adresy nebo orientačního bodu.</p>
		      <form action="">
		        <label htmlFor="start" id="startlabel" className="inputlabel" aria-hidden="true">Cíl trasy:</label>
		        <input type="search" name="start" id="start" aria-labelledby="startlabel" className="inputside" />
						<Navlink value="Pokračovat" to="#/multiple" />
		      </form>
		    </div>
		  </div>
		);
	}
}



