import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Usage extends React.Component {

	componentDidMount() {
		document.title = 'Použití GPS lokace'
	}

	render() {
		return (
			<div className="main">
		      <p className="text focusable" tabIndex="-1">Mám použít tvou GPS lokaci jako start trasy?</p>
		      <form action="">
		        <Navlink value="Ano" to="#/walk"/>
		        <Navlink value="Ne" to="#/walk"/>
		        <Navlink value="Zpět na hledání" to="#/"/>
		      </form>
		  </div>
		);
	}
}



