import React from 'react';

const logo = require('../images/loading.gif');

export default class Loading extends React.Component {

	render() {
		return (
			<div className="loading">
				<div className="t">
					<div className="tr">
						<div className="tc va-m">
							<img src={logo} alt="Načítám" aria-hidden="true"/>
							<p>Načítám</p>
						</div>
					</div>
				</div>
			</div>
		);
	}
}



